/* ----------------- Estilos principales ----------------- */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(to right, rgba(43,54,171,1) 0%, rgba(66,161,123,1) 50%, rgba(149,187,194,1) 93%, rgba(149,187,194,1) 100%);
  background-size: 400%;
  animation: fondo-anim 10s infinite alternate;
  color: white;
  font-family: 'Ubuntu', sans-serif;
  min-height: 100vh;
}

header {
  font-size: 2rem;
}

/* ----------------- Formulario ----------------- */

header,
form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: white;
}

form button {
  color: #807e7e;
  background: #f7fffe;
  cursor: pointer;
  transition: all 0.3s ease;
}

form button:hover {
  background: #5f5e5e;
  color: white;
}

/* ----------------- Todo ----------------- */

.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-list {
  min-width: 30%;
  list-style: none;
}

.todo {
  margin: 0.5rem;
  background: white;
  font-size: 1.5rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}

.filter-todo {
  padding: 1rem;
}

.todo li {
  flex: 1;
}

.trash-btn,
.complete-btn {
  background: #4b4a4a;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
  height: 100%;
}

.complete-btn {
  background: rgb(11, 212, 162);
}

.todo-item {
  padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.boton-rojo{
  background-color: #7ab6a7;
  color: #cfcccc;
}

.contador-tareas{
  text-align: center;
  margin: 1rem;
  font-size: 1.3rem;
}

.todo-input{
  width: 30%;
  margin-left: 1rem;
}


/* ----------------- Select ----------------- */

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
  color: #424141;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
}

.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
}

.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #b6b4b4;
  cursor: pointer;
  pointer-events: none;
}

.select:hover{
  background: #6b6a6a;
}

/* ----------------- Animaci√≥n Background ----------------- */

@keyframes fondo-anim{
  0%{background-position: left;}
  100%{background-position: right;}
}